/* Reset basic styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body and HTML setup for full-height */
body,
html {
  height: 100%;
}

/* Container to center the form */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh; /* Full viewport height */
}

/* Form container styling */
.form-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 10, 8, 0.1);
  max-width: 400px;
  width: 100%; /* Ensures full width on smaller screens */
  display: flex;
  flex-direction: column;
  background-color: #e4e1e1;
}

/* Form heading styling */
h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/* Form item styling */
.ant-form-item {
  margin-bottom: 14px;
}

/* Input field styling */
input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #d1cfcf;
  border-radius: 8px;
  font-size: 16px;
}

/* Focus state for inputs */
input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus {
  border-color: #40a9ff;
  outline: none;
}

/* Button styling */
.btn-primary {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
}

/* Hover state for button */
.btn-primary:hover {
  background-color: #0056b3;
}

/* Responsive styling for small screens */
@media (max-width: 768px) {
  .form-container {
    max-width: 90%;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  input[type="text"],
  input[type="email"],
  input[type="password"],
  .btn-primary {
    font-size: 14px; /* Slightly smaller font for smaller devices */
  }
}

/* Link styling */
.custom-link {
  color: #007bff; /* Blue color */
  text-decoration: none; /* Remove underline */
  font-weight: bold;
  margin-left: 8px; /* Add some space to the left */
  transition: color 0.3s ease;
  padding: auto;
}

/* Hover state for link */
.custom-link:hover {
  color: #0056b3; /* Darker blue on hover */
  text-decoration: underline; /* Underline on hover */
}
